export const randomInteger = (min, max) => {
    let rand = min + Math.random() * (max + 1 - min);
    rand = Math.floor(rand);
    return rand;
};

export const collision = (dom, dom2, isRect) => {
    const disX = dom.x - dom2.x;
    const disY = dom.y - dom2.y;
    const dw = dom.width + dom2.width;

    if (Math.abs(disX) > dw || Math.abs(disY) > dom.height + dom2.height) {
        return false;
    }

    return isRect ? true : Math.hypot(disX, disY) < dw / 2;
};

export const getAngle = (x, y) => {
    let angle = Math.atan(Math.abs(x / y));

    // calculate angle, value is 0-360
    if (x > 0 && y < 0) {
        angle = Math.PI - angle;
    } else if (x < 0 && y < 0) {
        angle = Math.PI + angle;
    } else if (x < 0 && y > 0) {
        angle = Math.PI * 2 - angle;
    }

    return angle;
};

export const rotatePoint = (pivotX, pivotY, pointX, pointY, angle) => {
    // Rotate clockwise, angle in radians
    const x = Math.cos(angle) * (pointX - pivotX) - Math.sin(angle) * (pointY - pivotY) + pivotX;

    const y = Math.sin(angle) * (pointX - pivotX) + Math.cos(angle) * (pointY - pivotY) + pivotY;

    return {
        x,
        y
    };
};

export const getFoodAngle = (cx, cy, ex, ey) => {
    const dy = ey - cy;
    const dx = ex - cx;
    let theta = Math.atan2(dy, dx);
    theta *= 180 / Math.PI; // rads to degs, range (-180, 180]
    if (theta < 0) {
        theta = theta * -1 + 180; // Turn into 0-360
    }
    return theta;
};
export const getBotAngle = (dx, dy) => {
    let theta = Math.atan2(dy, dx);
    theta *= 180 / Math.PI; // rads to degs, range (-180, 180]
    if (theta < 0) {
        theta = theta * -1 + 180; // Turn into 0-360
    }
    return theta;
};

export const velocityFromAngle = (angle, speed) => {
    let vx = Math.abs(speed * Math.sin(angle));
    let vy = Math.abs(speed * Math.cos(angle));

    if (angle < Math.PI / 2) {
        vy *= -1;
    } else if (angle < Math.PI) {
        vx *= 1;
        vy *= 1;
    } else if (angle < (Math.PI * 3) / 2) {
        vx *= -1;
    } else {
        vx *= -1;
        vy *= -1;
    }
    return {
        vx,
        vy
    };
};
